import App from './app';
import lazyObserver from './lazy';
import './owl-carousel';

(($) => {
  const slider = document.getElementById('js-slider-main');
  function initSlider() {
    if (slider && slider.children.length > 1) {
      $(slider).addClass('owl-carousel').owlCarousel({
        items: 1,
        nav: true,
        loop: true,
        dots: false,
        autoplay: true,
        autoplayTimeout: 5000,
        autoplayHoverPause: true,
        lazyLoad: true,
        onInitialized: () => {
          const cloned = slider.querySelectorAll('.owl-item.cloned .js-lazy-img');
          [...cloned].forEach(item => lazyObserver.observe(item));
        },
      });
    }
  }
  App.breakpoints.onBreakpoint(
    'xxs xs sm',
    false,
    () => {
      initSlider();
    },
  );
})(jQuery);
